.bpstory-container {
    .list-item {
        margin-left: 7px!important;
        margin-right: 7px!important;
        margin-bottom: 5px!important;
    } 
    .list-item .list-item-image, .story-box-opener .user-image{
        border-radius: 50%!important;
    }
    .story-box-opener .user-image {
        margin: 0!important;
        margin-right: 0!important;
    }
    .story-box .story-item .story-item-content,
    .story-box .story-box-opener-container, 
    .story-box .story-item-wrapper, 
    .story-box .story-item,
    .story-box {
        border-radius: 12px!important;
    }
    .direction-btn {
        width: 28px!important;
        height: 28px!important;
        background: #bbb!important;
        border-radius: 50%!important;
        position: absolute!important;
        top: calc(50% - 14px)!important;
        transition: .15s linear!important;
        cursor: pointer!important;
        border: none!important;
        outline: none!important;
        z-index: 8!important;
        opacity: .6!important;
    }
}