.bpstory-container .list-item {
  margin-left: 7px !important;
  margin-right: 7px !important;
  margin-bottom: 5px !important; }

.bpstory-container .list-item .list-item-image, .bpstory-container .story-box-opener .user-image {
  border-radius: 50% !important; }

.bpstory-container .story-box-opener .user-image {
  margin: 0 !important;
  margin-right: 0 !important; }

.bpstory-container .story-box .story-item .story-item-content,
.bpstory-container .story-box .story-box-opener-container,
.bpstory-container .story-box .story-item-wrapper,
.bpstory-container .story-box .story-item,
.bpstory-container .story-box {
  border-radius: 12px !important; }

.bpstory-container .direction-btn {
  width: 28px !important;
  height: 28px !important;
  background: #bbb !important;
  border-radius: 50% !important;
  position: absolute !important;
  top: calc(50% - 14px) !important;
  transition: 0.15s linear !important;
  cursor: pointer !important;
  border: none !important;
  outline: none !important;
  z-index: 8 !important;
  opacity: 0.6 !important; }
